<template>
  <div id="app">
    <div id="outside-box">
      <div v-for="i in this.floorCount/2" :key="i">
        <outside :floorId="i" @outsideCallFloorUp="callUpHelp" @outsideCallFloorDown="callDownHelp"
          style="margin-left:20px" ref="outsideChild">
        </outside>
      </div>
    </div>
    <br>
    <br>
    <div id="outside-box">
      <div v-for="i in this.floorCount/2" :key="i">
        <outside :floorId="i+10" @outsideCallFloorUp="callUpHelp" @outsideCallFloorDown="callDownHelp"
          style="margin-left:14px" ref="outsideChild">
        </outside>
      </div>
    </div>
    <div id="inside-box">
      <div v-for="o in this.elevatorNum" :key="o">
        <elevator :elevatorId=o :floorCount="floorCount" :pressFloor="pressFloor" :isUp="isUp"
          @elevatorStatus="getElevatorStatus" ref="elevatorChild">
        </elevator>
      </div>
    </div>
  </div>
</template>
<!-- ‰ªéelevatorÁöÑÂÑøÂ≠ê‰º†Áà∏Áà∏Â•ΩÂÉèÊòØÂÆåÊàê‰∫Ü -->
<!-- ‰ªéoutsideÁöÑÂÑøÂ≠ê‰º†Áà∏Áà∏Â•ΩÂÉè‰πüÂÆåÊàê‰∫Ü -->
<!-- ‰∏ãÈù¢ÂÜôCalculateÂáΩÊï∞ÂíØ -->
<!-- CalculateÁªìÊùüÔºåÂ∞±ÊòØÊääpressËøô‰∏ÄÂ±ÇÂä†Âà∞Êüê‰∏ÄelevatorÁöÑÊ∂àÊÅØÂØπÂàóÈáåÈù¢ÔºåÂπ∂‰∏îËøôÈáåË¶ÅÊ≥®ÊÑèÈÅøÂÖçÈáçÂ§çÊ∑ªÂä†ÔºåËøôÂ∞±‰ΩìÁé∞isselect„Äê20„ÄëÁöÑ‰ºòË∂äÊÄß -->
<script>
  import elevator from "./components/elevator"
  //  import card from "./components/card"
  import outside from "./components/outside"
  //import elevator from "./components/elevator"
  export default {
    name: 'App',
    components: {
      //elevator,
      //  card,
      elevator,
      outside,
    },
    data() {
      return {
        elevatorNum: 5,
        floorCount: 20,
        isAllNotOk: false,
        elevatorIsOK: [...Array(5)].map(() => true),
        cost: [...Array(5)].map(() => 0),
        aimFloor: [...Array(5)].map(() => 0),
        goingUp: [...Array(5)].map(() => 0),              //ÊØè‰∏™ÁîµÊ¢ØÁöÑÁä∂ÊÄÅÔºå1‰ª£Ë°®‰∏äÂçáÔºå-1‰ª£Ë°®‰∏ãÈôçÔºå0‰ª£Ë°®Á¶ÅÊ≠¢
        currentFloor: [...Array(5)].map(() => 0),        //ÊØè‰∏™ÁîµÊ¢ØÂΩìÂâçÁöÑÊ•ºÂ±Ç-1,ÈúÄË¶ÅÊî∂Âà∞ËØ∑Ê±Ç
        CallElevatorId: 0,        //Áî±Âì™‰∏™ÁîµÊ¢ØÊù•ÂõûÂ∫îËØ∑Ê±Ç   
        pressFloor: -1,          //‰ª£Ë°®Ë¢´Êåâ‰∏ãÁöÑÊ•ºÂ±Ç-1ÔºàÂõ†‰∏∫Â≠êÁªÑ‰ª∂‰∏≠ÁöÑisselectÊòØ‰ªé0ÂºÄÂßãÁöÑÔºâ
        isUp: false,             //Ëøô‰∏™Âè™ÊúâÂú®pressFloor‰∏çÊòØ-1Êó∂ÊâçÊúâÊÑè‰πâ
      }
    },
    methods: {
      insertToElevator() {
        //ÊääpressfloorÂä†Âà∞ÊåáÂÆöÁöÑÁîµÊ¢ØÁöÑisselectÈáåÈù¢
        //Èó≤È∫ªÁÉ¶ÔºåÁõ¥Êé•ref‰∫Ü
        this.$refs.elevatorChild[this.CallElevatorId].insertFromOutside(
          this.pressFloor
        )
        console.log("‰º†ÂÖ•Á¨¨Âá†‰∏™ÁîµÊ¢ØÔºà‰ªé0ÂºÄÂßãÔºâÔºö",
                this.CallElevatorId,
                "Á¨¨Âá†Â±ÇÊ•º",
                this.pressFloor)
      },
      calculateCallElevatorId() {
        //Âú®callhelpÂêé‰ΩøÁî®
        if (this.isUp) {
          //Áî®Êà∑ÈúÄË¶Å‰∏äÊ•º
          for (let i = 0; i < this.elevatorNum; i++) {
            if (this.goingUp[i] === 0) {
              //Â¶ÇÊûúÊ≠§ÁîµÊ¢ØÈùôÊ≠¢
              this.cost[i] = Math.abs(this.currentFloor[i] - this.pressFloor);
            }
            else if (this.goingUp[i] === 1) {
              //Â¶ÇÊûúÁîµÊ¢Ø‰∏äÂçá
              if (this.pressFloor >= this.currentFloor[i]) {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏äËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ§ß‰∫éÁ≠â‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.pressFloor - this.currentFloor[i];
              }
              else {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏äËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ∞è‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.aimFloor[i] - this.currentFloor[i] + this.aimFloor[i] - this.pressFloor;
              }
            }
            else {
              //Â¶ÇÊûúÁîµÊ¢Ø‰∏ãÈôç
              if (this.pressFloor <= this.currentFloor[i]) {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏ãËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ∞è‰∫éÁ≠â‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.currentFloor[i] - this.aimFloor[i] + this.pressFloor - this.aimFloor[i];
              }
              else {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏ãËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ§ß‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.currentFloor[i] - this.aimFloor[i] + this.pressFloor - this.aimFloor[i];
              }
            }
          }
        }
        else {
          //ËØ¥ÊòéÁî®Êà∑ÊÉ≥Âêë‰∏ãËµ∞
          for (let i = 0; i < this.elevatorNum; i++) {
            if (this.goingUp[i] === 0) {
              //Â¶ÇÊûúÊ≠§ÁîµÊ¢ØÈùôÊ≠¢
              this.cost[i] = Math.abs(this.currentFloor[i] - this.pressFloor);
            }
            else if (this.goingUp[i] === 1) {
              //Â¶ÇÊûúÁîµÊ¢Ø‰∏äÂçá
              if (this.pressFloor >= this.currentFloor[i]) {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏äËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ§ß‰∫éÁ≠â‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.aimFloor[i] - this.currentFloor[i] + this.aimFloor[i] - this.pressFloor;
              }
              else {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏äËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ∞è‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.aimFloor[i] - this.currentFloor[i] + this.aimFloor[i] - this.pressFloor;
              }
            }
            else {
              //Â¶ÇÊûúÁîµÊ¢Ø‰∏ãÈôç
              if (this.pressFloor <= this.currentFloor[i]) {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏ãËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ∞è‰∫éÁ≠â‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = Math.abs(this.currentFloor[i] - this.pressFloor);
              }
              else {
                //Ëã•ÊüêÁîµÊ¢ØÊ≠£Âú®Âêë‰∏ãËøêÂä®Âπ∂‰∏îÁî®Êà∑ÈÄâÊã©Ê•ºÂ±ÇÂ§ß‰∫éËØ•ÁîµÊ¢ØÂΩìÂâçÊ•ºÂ±Ç
                this.cost[i] = this.currentFloor[i] - this.aimFloor[i] + this.pressFloor - this.aimFloor[i];
              }
            }
          }
        }
        //‰ª•‰∏äÊòØÂàùÂßãÂåñcostÊï∞ÁªÑÔºå‰∏ãÈù¢Ê±ÇÂá∫ÊúÄÂ∞èÁöÑ
        //Ê≥®ÊÑèËøôÈáåÊúÄÂ∞èÁöÑÊòØÂú®Ê≤°ÊúâÊïÖÈöúÁöÑÁîµÊ¢Ø‰∏≠ÊâæÁöÑ
        
        var min;
        var min_id;
        for(let i=0;i<5;i++)
        {
          if(this.elevatorIsOK[i] === true)
          {
            min = this.cost[i];
            min_id = i;
            break;
          }
        }
        for (let i = 0; i < 5; i++) {
          if(this.elevatorIsOK[i] === false)
          {
            continue;
          }
          else 
          {
              if (this.cost[i] < min) {
              min = this.cost[i];
              min_id = i;
            }
          }
        }
        this.CallElevatorId = min_id;
        console.log("ËÆ©ÊàëÂ∫∑Â∫∑Ë∞ÅÊù•Êé•Êàë", this.CallElevatorId);
      },
      getElevatorStatus(
        elevator_id,
        elevator_currentFloor,
        elevator_isGoingUp,
        elevator_aimFloor,
        elevator_isOk,
      ) {
        //this.isOk[elevator_id - 1] = elevator_isOk;
        //Êúâ‰∏™ÈóÆÈ¢òcurrentFloorÊ≤°ÊúâÂÆûÊó∂Êõ¥Êñ∞ÔºåÊâÄ‰ª•Êàë‰ª¨Âú®elevatorÈáåÈù¢currentfloorÂèò‰πüÈúÄË¶Å‰∏äÊä•Áà∂ÁªÑ‰ª∂
        var i = 0;
        this.elevatorIsOK[elevator_id - 1] = elevator_isOk;
        this.currentFloor[elevator_id - 1] = elevator_currentFloor;
        //Êúâ‰∏™ÈóÆÈ¢ògoingUpÂú®ÂÅú‰∏ãÊù•ÁöÑÊó∂ÂÄôÊ≤°ÊúâÂèòÈõ∂
        this.goingUp[elevator_id - 1] = elevator_isGoingUp;
        this.aimFloor[elevator_id - 1] = elevator_aimFloor;
        for(i=0;i<5;i++)
        {
          if(this.elevatorIsOK[i])
          {
            break;
          }
        }
        if(i==5)
        {
          this.isAllNotOk = true;
        }
      },
      callUpHelp(floor_id) {
        if(this.isAllNotOk === false)
        {
          //Êàë‰ª¨Â∏åÊúõËøô‰∏™‰πü‰ªéÈõ∂ÂºÄÂßãÔºåËøôÊ†∑ËÆ°ÁÆóÊñπ‰æø
          this.pressFloor = floor_id - 1;
          this.isUp = true;
          //‰∏ãÈù¢ÁªèËøáËÆ°ÁÆóÂáΩÊï∞ÔºåÊääËøô‰∏™ÈúÄÊ±ÇÂàÜÈÖçÁªôÊüê‰∏Ä‰∏™ÁîµÊ¢ØÔºåÂç≥Âä†ÂÖ•Ëøô‰∏™ÁîµÊ¢ØÁöÑisselect
          //Êàë‰ª¨Â∏åÊúõÂè™Âú®Áé∞Âú®ÂèØ‰ª•Ë∑ëÁöÑÁîµÊ¢ØÈáåÈù¢Êâæ
          this.calculateCallElevatorId()
          this.insertToElevator();
        }
        else
        {
          alert("ÊâÄÊúâÁîµÊ¢ØÈÉΩÂùèÂï¶üÜò");
        }
      },
      callDownHelp(floor_id) {
        if(this.isAllNotOk === false)
        {
          this.pressFloor = floor_id - 1;
          this.isUp = false;
          this.calculateCallElevatorId()
          this.insertToElevator();
        }
        else
        {
          alert("ÊâÄÊúâÁîµÊ¢ØÈÉΩÂùèÂï¶üÜò");
        }
      },
    }
  }
</script>

