<template>
  <!-- ÂàùÂßãÂêåÊó∂ÁÇπÂáª‰∏ãÂíå‰∏äÊúâÈóÆÈ¢òÔºå‰ΩÜÊòØ‰∏äÂíå‰∏ãÊ≤°ÈóÆÈ¢ò -->
  <!-- ÂéüÂõ†ÊòØaimfloorÂú®ÊîπÂèòÊñπÂêëÁöÑÊó∂ÂÄôÊ≤°ÊúâÊõ¥Êñ∞Ôºå‰∏ä‰∏ãÈÉΩÊúâËøô‰∏™ÈóÆÈ¢ò -->
  <!-- ÊâÄ‰ª•Êàë‰ª¨Âú®updateÈÇ£ÈáåÂÅö‰∫ÜÁÇπÂ§ÑÁêÜ -->
  <!-- ËøòÊúâÁÇπÂáªÂêéÁöÑcssÊ†∑ÂºèÊ≤°ÊêûÂ•Ω -->
  <div id="lay">
    <el-card class="box-card" :class="{boxMap: isOk === false}">
      <el-card :class="{map1:isGoingUp===0, map2:!(isGoingUp===0)}" shadow="always">
        <div>
          <i class="el-icon-caret-top" v-show="this.isGoingUp===1"></i>
          <i class="el-icon-caret-top disabled" v-show="this.isGoingUp!==1"></i>
          {{ this.currentFloor+1}}F
          <i class="el-icon-caret-bottom" v-show="this.isGoingUp===-1"></i>
          <i class="el-icon-caret-bottom disabled" v-show="this.isGoingUp!==-1"></i>
        </div>
        <div>{{msg}}</div>
      </el-card>
      <div class="button-group">
        <div v-for="i in this.floorCount" :key="i" class="button-place">
        <el-button type="primary" :class="{button:buttonClicked[i-1] === false , button2:buttonClicked[i-1] === true } " @click="handleInsideButtonClick(i)">
            {{ i }}
          </el-button>
        </div>
      </div>
      <div class="control">
        <el-switch style="display: block" v-model="isOpen" active-color="#13ce66" inactive-color="#ff4949"
          active-text="ÂºÄÈó®" inactive-text="ÂÖ≥Èó®" class="switch" @click.native="doorAlert()">
        </el-switch>
      </div>
      <div class="accident">
        <el-button type="primary" icon="el-icon-phone" style="margin-left:-5px;background-color:#11d82b;border:none"  @click = "askForCommunicate()">
        </el-button>
        <el-button type="primary" icon="el-icon-message-solid"
          style="margin-left:80px;background-color:#e7370a;border:none" @click = "askForHelp()"></el-button>
      </div>
    </el-card>
    <el-card>
      <div v-for="i in this.floorCount" :key="i" class="floor-place">
        <el-button type="primary"  :class = "{elevatorFloor: (20-i) === currentFloor, elevatorFloor2:(20-i) != currentFloor}" >
            {{ 21-i }}
          </el-button>
        </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: "card",
    props: {
          elevatorId: Number,                //ÁîµÊ¢ØÁºñÂè∑
          floorCount: Number,                //ËøôÈáåÊòØ20
          pressFloor: Number,                //Áà∂ÁªÑ‰ª∂ÁöÑÊåâ‰∏ãÊ•ºÂ±ÇÔºàÊ≥®ÊÑèÔºåËøôÈáåÊòØÂ§ñË∞ÉÂ∫¶ÂêéÈÄâÊã©ÁöÑÁîµÊ¢ØÔºâ
          isUp: Boolean,                     //ÁúãÁúãÁà∂ÁªÑ‰ª∂Êåâ‰∏ãÁöÑÊ•ºÂ±ÇÊòØÂêë‰∏äËøòÊòØÂêë‰∏ã
          //ÂΩìÂ§ñÈÉ®Áî®Êà∑Êåâ‰∏ãÊ•ºÂ±ÇÂêéÔºåÁà∂ÁªÑ‰ª∂Â∞±ÂêëÂ≠êÁªÑ‰ª∂‰º†ÈÄípressFLoorÂíåisUp
      },
    data() {
      return {
        buttonClicked: [...Array(20)].map(() => false),    // ÂÜÖÈÉ®ËØ∑Ê±ÇÈòüÂàó,Èò≤Ê≠¢ÈáçÂ§çÁÇπÂáªÂä†ÂÖ•ÈòüÂàóÂ§ö‰∏™Áõ∏ÂêåÂÄºÔºåÊâÄ‰ª•Ëøô‰πàËÆæÁΩÆ‰∫Ü
        currentFloor: 0,    //ÂΩìÂâçÊ•ºÂ±Ç-1
        isOpen: false,      //ÊåáÈó®ÂºÄ‰∫ÜÊ≤°
        aimFloor: 0,
        msg: "Á©∫Èó≤‰∏≠",
        isGoingUp: 0,
        isSelect: [...Array(20)].map(() => false),
        timer: null,    // ÂÜÖÈÉ®ËØ∑Ê±ÇÈòüÂàó
        cnt: 0,
        isOk: true,
      //   selfcolor: {
      //       backgroundColor:'hsl(220, 8%, 93%)',
      //   },
      }
    },
    methods: {
      askForCommunicate()
      {
        if(this.isOk === true)
        {
          alert("Áé∞Âú®ÁîµÊ¢ØÊòØÂ•ΩÁöÑÔºåÁª¥‰øÆ‰∏™üî®");
        }
        else 
        {
          alert("ËØ∑Ê±Ç‰∏éÁîµÊ¢ØÁÆ°ÁêÜÂëòÈÄöËØùüÜò,Âø´ÁÇπÁª¥‰øÆÁîµÊ¢ØÂïäÂïäÂïä");
          this.msg = "Áª¥‰øÆ‰∏≠";
          setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
            //Áª¥‰øÆÂ•Ω‰∫ÜÔºåÊàë‰ª¨ËÆ©‰ªñÂõûÂà∞ÂàùÂßãÁä∂ÊÄÅ
            this.isOk = true;
            this.msg = "Á©∫Èó≤‰∏≠";
            this.currentFloor = 0;
            this.isOpen = false;
            this.aimFloor = 0;
            this.isGoingUp = 0;
            this.timer = null;
            this.cnt = 0;
            for(let i=0;i<20;i++)
            {
              this.$set(this.isSelect, i, false);
              this.$set(this.buttonClicked, i, false);
            }
          }, 3000);
          //ÁÑ∂ÂêéË¶ÅËÆ©‰ªñÁªßÁª≠ËøõË°åÂéüÊù•ÁöÑÊ¥ªÂä®
          this.updateStateToFather();
        }
      },
      isEmpty() {
        for (let i = 0; i < 20; i++) {
          if (this.isSelect[i]) {
            return false;
          }
        }
        return true;
      },
      updateStateToFather()
      {
          //ÂΩìÊØèÊ¨°Êõ¥Êñ∞Áä∂ÊÄÅÂêéÈÉΩÂêëÁà∂ÁªÑ‰ª∂‰º†ÈÄíÊúÄÊñ∞ÊÉÖÂÜµ
          this.$emit(
              "elevatorStatus",
              this.elevatorId,
              this.currentFloor,
              this.isGoingUp,
              this.aimFloor,
              this.isOk
          )
      },
      askForHelp()
      {
        alert("ËØ∑Ê±ÇÁîµÊ¢ØÁÆ°ÁêÜÂëòÊè¥Âä©üî•");
        //‰ΩÜÊòØÊàë‰ª¨ËøòÊòØ‰øùÂ≠ò‰∫ÜÂéüÊù•ÁöÑ‰ªªÂä°Âπ∂Ê≤°ÊúâÊ∏ÖÁ©∫
        this.isOk = false;
        this.isGoingUp = 0;
        this.isOpen = false;
        this.updateStateToFather()
        setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
          this.msg="ÁîµÊ¢ØÊïÖÈöúÔºå‰∏çËÉΩ‰ΩøÁî®üÜò";
        }, 1000);
      },
      openDoor() {
        this.isOpen = true;
        this.msg = "Âø´ËøõÂá∫ÁîµÊ¢ØÂìá";
        this.isSelect[this.currentFloor] = false;
        //ÊîπÂèò‰∫ÜÊï∞ÁªÑÂÖÉÁ¥†Áä∂ÊÄÅÔºåÊàë‰ª¨Ë¶ÅÈáçÊñ∞Êõ¥Êñ∞ÁîµÊ¢ØÁä∂ÊÄÅ
        this.buttonClicked[this.currentFloor] = false;
        this.updateState();
        if(this.isEmpty())
        {
            this.isGoingUp = 0;
        }
        this.updateStateToFather();
        console.log("Ê≠§Êó∂ÁõÆÊ†áÊ•ºÂ±Ç  " + this.aimFloor + "ÂΩìÂâçÊ•ºÂ±Ç  " + this.currentFloor);
        //ÊàëÂºÄÈó®‰∫Ü‰πãÂêéÊòØËÇØÂÆöË¶ÅÂÖ≥Èó®ÁöÑ
        console.log("Ê≠§Êó∂ÁîµÊ¢ØÁä∂ÊÄÅ", this.isGoingUp);
        //ÊúâÊó∂Êàë‰ª¨Âà∞È°∂‰∫ÜÔºåaim‰∏∫ËÆæ‰∏∫‰∫Ü-1Ôºåisgoingup‰∏∫-1‰∫ÜÔºå‰ΩÜÊòØÂêéÈù¢ÂÖ≥Èó®Êó∂ÂÄôÊ£ÄÊµãÁ©∫ÂèàÊîπ‰∏∫0‰∫Ü
        setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
          this.closeDoor();
        }, 3000);
      },
      //Êàë‰ª¨ËßÑÂÆöÂè™ÊúâÂÖ≥Èó®ÊâçËÉΩÊåâÊåâÁ∫Ω
      closeDoor() {
        //ËøôÈáåÊòØÂà∞ËææËØ•Â±ÇÂêé
        this.isOpen = false;
        //Ê≥®ÊÑèÂú®ÂÖ≥Èó®‰πãÂâçÊàë‰ª¨ÊâßË°å‰∫ÜpopÊìç‰Ωú
        //ËøôÈáåÊàë‰ª¨ÂÖ≥Èó®ÂêéÈúÄË¶ÅÂà§Êñ≠ËøòÈúÄ‰∏çÈúÄË¶ÅÁªßÁª≠run
        //Á¨¨‰∏ÄÁßçÊÉÖÂÜµÊòØÁîµÊ¢Ø‰∏äÂçáÊàñËÄÖ‰∏ãÈôç‰∏≠ÈÄîÂÅú‰∏ãÔºåÁªßÁª≠runÔºåÂπ∂‰∏îgoingupÁª¥ÊåÅÂéüÊù•
        //Á¨¨‰∫åÁßçÊÉÖÂÜµÊòØÁîµÊ¢Ø‰∏äÂçáÊàñËÄÖ‰∏ãÈôçÂà∞‰∫ÜaimFloor‰∫ÜÔºåÈòüÂàóÈáåÈù¢Ê≤°‰∏úË•ø‰∫ÜÔºåÂ∞±ÈöîÁùÄÂê¨ÁùÄÔºåÁõ¥Êé•goingup‰∏∫0Ôºå‰∏çrun‰∫Ü
        //Á¨¨‰∏âÁßçÊÉÖÂÜµÊòØÁîµÊ¢Ø‰∏äÂçáÂà∞‰∫ÜaimFloorÔºå‰ΩÜÊòØËøòÊúâ‰∏ãÈù¢Ê•ºÂ±ÇÈúÄË¶ÅÂìçÂ∫îÔºåÊîπgoingupÂÜçupdateÂú®run
        if(this.isOk)
        {
            if (this.isEmpty()) {
            this.msg = "Á©∫Èó≤‰∏≠";
            this.isGoingUp = 0;
            this.cnt = 0;
            //this.cnt = 0;
            for(var i = 0;i<20;i++)
            {
              this.$set(this.isSelect, i, false);
              this.$set(this.buttonClicked,i,false);
            }
          }
            else {
              this.msg = "ÂøôÁ¢å‰∏≠";
              setTimeout(this.run, 1000);
            }
        }

      },
      checkFromBottom() {
        for (let i = 0; i <= this.currentFloor; i++) {
          if (this.isSelect[i]) {
            return i;
          }
        }
        return -1;
      },
      checkFromTop() {
        for (let i = 19; i >= this.currentFloor; --i) {
          if (this.isSelect[i]) {
            return i;
          }
        }
        return -1;
      },
      updateState() {
        //ÂÜçÊõ¥ÊîπÂä®ÊÄÅÊåáÊ†á
        if (this.isGoingUp === 1) {
          //‰∏äÂçá
          this.aimFloor = this.checkFromTop();
          if (this.aimFloor == -1) {
            //Âê¶Âàô‰∏äÂçáËΩ¨‰∏ãÈôçaimÂ∞±‰∏ÄÁõ¥ÊòØ-1
            this.aimFloor = this.checkFromBottom();
          }
        }
        else if (this.isGoingUp === -1) {
          //‰∏ãÈôç
          this.aimFloor = this.checkFromBottom();
          if (this.aimFloor == -1) {
            //Âê¶Âàô‰∏ãÈôçËΩ¨‰∏äÂçáaimÂ∞±‰∏ÄÁõ¥ÊòØ-1
            this.aimFloor = this.checkFromTop();
          }
        }
        else {
          //ÂΩìÁîµÊ¢ØÊ≠§Êó∂ÂÅú‰∏ãÊòØÔºåÊàë‰ª¨ÂÖà‰ªé‰∏äÂêë‰∏ã
          //Â¶ÇÊûúÊâæ‰∏çÂà∞Âú®ÂÜ≤‰∏ãÂ≤õ‰∏ä
          this.aimFloor = this.checkFromTop();
          if (this.aimFloor === -1) {
            this.aimFloor = this.checkFromBottom();
          }
        }
        if (this.currentFloor === this.aimFloor) {
          this.isGoingUp = 0;
        }
        else if (this.currentFloor > this.aimFloor) {
          this.isGoingUp = -1;
        }
        else {
          this.isGoingUp = 1;
        }
      },
      insertFromOutside(pressFloor) {
          //ËøôÈáåÊî∂Âà∞ÁöÑpressFloorÊòØÁà∂ÁªÑ‰ª∂‰º†ËøáÊù•ÁöÑ
          console.log("Áà∂ÁªÑ‰ª∂Ëß¶ÂèëÊàë‰∫Ü,ÊàëÊòØÁîµÊ¢Ø(‰ªé1ÂºÄÂßãÔºâ", this.elevatorId)
          console.log("Áé∞Âú®Êåâ‰∏ãÁ¨¨Âá†Â±ÇÔºü", pressFloor)
          if(pressFloor == this.currentFloor)
          {
            this.isOpen = true;
            setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
          this.isOpen = false;
        }, 3000);
          }
          else 
          {
            this.isSelect[pressFloor] = true;
            this.updateState();
            if (this.cnt < 1) {
                //ËøôÈáå‰∏éÂÜÖÈÉ®ÁÇπÂáªÂ§ÑÁêÜÊòØÁõ∏ÂêåÁöÑ
            this.run();
          }
        }
      },
      handleInsideButtonClick(floor) {
        //ÂÖàÊîπÊï∞ÁªÑ‰ø°ÊÅØ
        if(this.isOk)
        {
            if(floor-1 == this.currentFloor)
            {
              this.isOpen = true;
            }
            else 
            {
              this.$set(this.buttonClicked, floor - 1, true);
              console.log("ÂÜÖÈÉ®ÈòüÂàóÊàëÂ∫∑Â∫∑  ", this.isSelect);
              console.log("ÂΩìÂâçÁÇπÂáªÊ•ºÂ±ÇÔºà‰ªé1ÂºÄÂßãÔºâ " + floor);
              this.$set(this.isSelect, floor - 1, true);
              this.updateState(floor);
              this.updateStateToFather();
              //ÂÜçËøõË°årunÁöÑÈÄªËæë
              console.log("ÂΩìÂâçÁîµÊ¢ØÁõÆÊ†áÊ•ºÂ±Ç  " + this.aimFloor);
              console.log("ÂΩìÂâçÁîµÊ¢ØÁä∂ÊÄÅ " + this.isGoingUp);
              if (this.cnt < 1) {
                //Êàë‰ª¨‰∏Ä‰∏™ÁîµÊ¢ØÂè™ÂàõÂª∫‰∏Ä‰∏™ËøêË°åÊ¥ªÂä®Ôºå‰∏çÁÑ∂ËÆ°Êó∂Âô®‰ºöÂÜ≤Á™Å
                //‰ΩÜÊòØÊàë‰ª¨ÂΩìÁîµÊ¢ØÂÅú‰∏ãÊù•Êó∂Â∞±Á≠â‰ª∑‰∏éËøõÂÖ•ÂàùÂßãÁä∂ÊÄÅÔºå‰ºöÊääcntÈáçÊñ∞‰∏∫0
                console.log("cnt  " + this.cnt);
                this.run();
                }
            } 
        }
        else
        {
          alert("Ëøô‰∏™ÁîµÊ¢ØÂ∑≤ÁªèÂùè‰∫ÜÔºå‰∏çË¶ÅÁÇπ‚ö†Ô∏è");
        }
      },
      //ÂÖà‰∏ãÂêé‰∏äÊúâÈóÆÈ¢ò
      //ÂÖà‰∏äÂêé‰∏ãÊöÇÊó∂Êú™Âá∫Áé∞Â§ßbug
      run() {
        //let _this = this
        //runÂíåupdateÂú®ÂæàÂ§öÊÉÖÂÜµ‰∏ãÂ•ΩÂÉèÊòØËøû‰ΩìÂ©¥ÂÑø
        //ËøêË°åÈÄªËæë
        this.msg = "ÂøôÁ¢å‰∏≠";
        if (this.isGoingUp !== 0) {
          console.log("1ÁõÆÊ†áÊ•ºÂ±Ç  " + this.aimFloor + "ÂΩìÂâçÊ•ºÂ±Ç  " + this.currentFloor);
          if (this.isGoingUp === 1) {
            //Âêë‰∏äËøêÂä®
            console.log("ÊàëÁöÑÂΩìÂâçÊ•ºÂ±ÇË¶ÅÂä†‰∏ÄÊãâ")
            //‰∏çÂä†Ëøô‰∏™if‰ºöÂ§ö‰∏ÄÂ±Ç
            if (!this.isSelect[this.currentFloor]) {
              setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
                this.currentFloor++;
                this.updateStateToFather()
              }, 1000);
            }
            console.log("ÁõÆÊ†áÊ•ºÂ±Ç  " + this.aimFloor + "ÂΩìÂâçÊ•ºÂ±Ç  " + this.currentFloor);
            //Â¶ÇÊûúËøôÂ±ÇÊòØÂÜÖÈÉ®ËØ∑Ê±ÇÊàñËÄÖÂ§ñÈÉ®ËØ∑Ê±ÇÔºåÂ§ñÈÉ®ËØ∑Ê±ÇÂÖà‰∏çËÄÉËôë
            //ÈúÄË¶ÅÂú®ËøôÈáåÂÅú‰∏ãÂºÄÈó®ÔºåÂÜçÂÖ≥Èó®ÔºåÂÜçÁªßÁª≠Ë∑ë
            console.log(this.currentFloor);
            if (this.isSelect[this.currentFloor] === true) {
              setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
                this.openDoor();
              }, 1000);
            }
            else {
              //clearInterval(this.timer)
              //Êàë‰ª¨Ê≥®ÊÑèËøôÈáåÊàë‰ª¨‰∏çÈááÁî®intervalÊù•ËøõË°årunÔºåËÄåÊòØÂª∂ËøüÈÄíÂΩíË∞ÉÁî®
              //ËøôÊ†∑ÂΩìelse‰∏ç‰ºö‰∏ÄË∑ØÈ£û‰∏äÂéªÔºåÂà∞‰∫ÜaimÂêéÂ∞±ÂÅú‰∫ÜÔºåÂèàÂõûÂà∞‰∫ÜÂâçÈù¢ÂºÄÈó®ÂÖ≥Èó®ÁöÑÈÄªËæë
              setTimeout(this.run, 1000);
              this.cnt++;
              console.log("ÂàõÂª∫‰∫ÜËÆ°Êó∂Âô®")
            }
          }
          else if (this.isGoingUp === -1) {
            //Âêë‰∏äËøêÂä®
            console.log("ÊàëÁöÑÂΩìÂâçÊ•ºÂ±ÇË¶ÅÂáè‰∏ÄÊãâ")
            //‰∏çÂä†Ëøô‰∏™if‰ºöÂ§ö‰∏ÄÂ±Ç
            if (!this.isSelect[this.currentFloor]) {
              setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
                this.currentFloor--;
                this.updateStateToFather()
              }, 1000);
            }
            console.log("ÁõÆÊ†áÊ•ºÂ±Ç  " + this.aimFloor + "ÂΩìÂâçÊ•ºÂ±Ç  " + this.currentFloor);
            //Â¶ÇÊûúËøôÂ±ÇÊòØÂÜÖÈÉ®ËØ∑Ê±ÇÊàñËÄÖÂ§ñÈÉ®ËØ∑Ê±ÇÔºåÂ§ñÈÉ®ËØ∑Ê±ÇÂÖà‰∏çËÄÉËôë
            //ÈúÄË¶ÅÂú®ËøôÈáåÂÅú‰∏ãÂºÄÈó®ÔºåÂÜçÂÖ≥Èó®ÔºåÂÜçÁªßÁª≠Ë∑ë
            console.log(this.currentFloor);
            if (this.isSelect[this.currentFloor] === true) {
              setTimeout(() => {   //ËÆæÁΩÆÂª∂ËøüÊâßË°å
                this.openDoor();
              }, 1000);
            }
            else {
              //clearInterval(this.timer)
              //Êàë‰ª¨Ê≥®ÊÑèËøôÈáåÊàë‰ª¨‰∏çÈááÁî®intervalÊù•ËøõË°årunÔºåËÄåÊòØÂª∂ËøüÈÄíÂΩíË∞ÉÁî®
              //ËøôÊ†∑ÂΩìelse‰∏ç‰ºö‰∏ÄË∑ØÈ£û‰∏äÂéªÔºåÂà∞‰∫ÜaimÂêéÂ∞±ÂÅú‰∫ÜÔºåÂèàÂõûÂà∞‰∫ÜÂâçÈù¢ÂºÄÈó®ÂÖ≥Èó®ÁöÑÈÄªËæë
              setTimeout(this.run, 1000);
              this.cnt++;
              console.log("ÂàõÂª∫‰∫ÜËÆ°Êó∂Âô®")
            }
          }
        }
      },
      doorAlert()
      {
        if(this.isGoingUp != 0)
        {
          alert("ÁîµÊ¢ØÊ≠£Âú®ËøêË°å‰∏çË¶ÅÂºÄÈó®ÂëÄüÜò ");
          this.isOpen = false;
        }
      },
    }
  }

